.home {
    width: 100%;
    align-items: center;
    font-family: "Arial", sans-serif;
    color: #3e497a;
  }
  
  .about {
    width: 100%;
    height: calc(115vh - 100px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #21325e;
    color: #f0f0f0;
    margin-top: 50px;
  }
  
  .about h2 {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 80px;
    color: #e9d35b;
    height: 50px;
  }
  
  .about .prompt {
    width: 60%;
    font-size: 30px;
  }

  .prompt {
    font-size: 40px;
  }

  .prompt svg {
    font-size: 60px;
    margin: 5px;
    color: white;
  }

  .icon-link {
    position: relative;
    display: inline-block;
    margin: 0 1px;
  }
  
  .icon-title {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    color: #e9d35b;
    font-size: 24px;
    position: absolute;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    opacity: 0;
    transition: opacity 0.3s, transform 0.3s;
    pointer-events: none;
  }
  
  .icon-link:hover .icon-title {
    opacity: 1;
    transform: translateX(-50%) translateY(-10px);
  }



  /* Responsive adjustments */
@media (max-width: 768px) {
  .about h2 {
    font-size: 40px; /* Smaller font size for small screens */
  }

  .about .prompt {
    width: 80%; /* Increased width for smaller screens */
    font-size: 20px; /* Smaller text for the prompt */
  }

  .prompt svg {
    font-size: 30px; /* Smaller icons for smaller screens */
  }

  .section {
    padding-top: 80px; 
    margin-top: -60px;
  }
}
  
  
  @media only screen and (max-width: 600px) {
    .about h2 {
      font-size: 40px;
    }
  
    .about .prompt {
      margin-top: 10px;
      font-size: 20px;
      width: 80%;
    }
  
    .skills {
      text-align: center;
    }
    .list {
      padding: 0;
    }
  
    .skills h2 {
      font-size: 30px;
    }

  }